import { useState, useEffect } from "react";
// import { Octokit } from "@octokit/core";
import { request } from "@octokit/request";
import {BrowserRouter as Router, Route, Routes} from 'react-router-dom';
import { useNavigate, Link } from "react-router-dom";

import Results from "./Results";
import RepoPage from "./RepoPage";

const Search = () => {

    //  useState
    const [username, setUsername] = useState('');
    const [repo, setRepo] = useState([]);
    const [loading, setLoading] = useState(true);
    const [repoName, setRepoName] = useState("");
    const [repoDescription, setRepoDescription] = useState("");
    const [httpFlag, setHttpFlag] = useState(true);
    const [searchFlag, setSearchFlag] = useState(false);
    const navigate = useNavigate();

    const handleChange = (e) => {
        setUsername(e.target.value);
    }

    const handleClick = async () => {
        // console.log(username);
        try {
            // const result = await axios(listUrl);
            const result =  await request('GET /users/{username}/repos', {
                username: username
            })
            console.log("useruser: ", username);
            console.log(result);
            setRepo(result);
            setHttpFlag(true);

        } catch(err) {
            if (err.name === "HttpError") {
                // let checkUser = [false];
                setHttpFlag(false);
            }
            
            // console.log(err.name);
        }
        console.log(repo);
        setSearchFlag(true);
        navigate(`/users/${username}/repos`);
    }

    console.log(httpFlag);

    console.log("continue");
    return ( 
        <div>
            {(searchFlag === false) ? (
            <div className="search">
                <input className="username" type="text" placeholder="Search" value={username} onChange={handleChange}></input>
                <button className="searchBtn" onClick={handleClick}>Enter</button>

            </div>
            ) : (<div />
                )}
            <div>  
                <Routes>
                    <Route path="/" element={<div />}></Route>
                    <Route path="/users/:username/repos" element={
                            httpFlag === true ? (
                                // <Results repo={ repo } header={ username }/>
                                <Results repo={ repo } />
                            ) : (
                                <div className="unvalidDiv">
                                    <Link to="/" className="backToSearch" onClick={() => {window.location.href="/"}}>Back</Link>
                                    <p className="unvalid">Unvalid User!</p>
                                </div>
                            )} 
                        />

                    <Route path="/users/:username/repos/:repoName" element={<RepoPage />}></Route>
                </Routes>
            </div>

        </div>
     );
}

export default Search;